import React, { useState } from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';
import { Button, Form, FormGroup, Label, Input, Row } from 'reactstrap';
//import StringFormatValidation from 'string-format-validation';
import InputMask from 'react-input-mask';



const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')

    }
}

const FormCapture = (props) => {

    const domainurl = window.location.host;
    const tokenId = 'A#FVV-0$6';

    const [phonenumber, setPhone] = useState('');
    const [d1, setD1] = useState('');
    const [d2, setD2] = useState('');
    const [m1, setM1] = useState('');
    const [m2, setM2] = useState('');
    // const [btnEnabled, setBtnEnabled] = useState(false);
    const dob = m1 + m2 + d1 + d2;

    const submitInfo = e => {
        e.preventDefault();

        if (phonenumber !== '' && dob !== '') {
            console.log(phonenumber + " | " + dob + " | " + domainurl + " | " + tokenId);
            localStorage.setItem('myPhone', phonenumber);
            localStorage.setItem('id', dob);
            props.onPageChange();
        }

    };

    // if (e.target.name === 'd2') {
    //     setBtnEnabled(true);
    // } else {
    //     setBtnEnabled(false);
    // }

    function moveCursor(e) {

        const form = e.target.form;
        const index = Array.prototype.indexOf.call(form, e.target);

       if (e.target.name === 'm1') {
            if (e.target.value.length === 1) {
                form.elements[index + 1].focus();
                e.preventDefault();
            }
        } else if (e.target.name === 'm2') {
            if (e.target.value.length === 1) {
                form.elements[index + 1].focus();
                e.preventDefault();
            }
        } else if (e.target.name === 'd1') {
            if (e.target.value.length === 1) {
                form.elements[index + 1].focus();
                e.preventDefault();
            }
        }
    };



    return (
        <StyleRoot>
            <div className="modalformcapture " style={styles.slideInLeft} >

                <div className="getloan ">
                    <h1 >ENTER THE 2 PIECES OF INFORMATION BELOW</h1>
                </div>
                <Form onSubmit={submitInfo}>
                    <div className='center'>
                        <div className='center_div dobAlignTop'>

                            <h4 style={style.generalText}  >10 Digit Mobile Phone Number</h4>

                        </div>

                        <div className='center_div' >
                            <FormGroup>
                                <Input autoFocus={true} onChange={e =>setPhone(e.target.value)} mask="999-999-9999" maskChar=" " tag={InputMask} required name='phonenumber' />
                            </FormGroup>

                        </div>

                        <div className='center_div dobAlign'>
                            <h4 style={style.generalText} >First 4 Digits of  Your Date of Birth</h4> <br />
                        </div>

                        <div className='center_div'>
                            <Row className='dobAlignRight'>
                                <Input pattern="\d*" name='m1' style={style.inputTag} onChange={e => { setM1(e.target.value); moveCursor(e); }} required placeholder='M'></Input>
                                <Input pattern="\d*" name='m2' style={style.inputTag} onChange={e => { setM2(e.target.value); moveCursor(e); }} required placeholder='M'></Input>
                                <Label pattern="\d*" className='stroke text-primary'>\</Label>
                                <Input pattern="\d*" name='d1' style={style.inputTag} onChange={e => { setD1(e.target.value); moveCursor(e); }} required placeholder='D' ></Input>
                                <Input pattern="\d*" name='d2' style={style.inputTag} onChange={e => { setD2(e.target.value); moveCursor(e); }} required placeholder='D'></Input>

                            </Row>

                        </div>

                        {/* {
                            form.DateOfBirthValid === false ? <p className='error text-danger '  >date of birth <b> MUST</b>  be 4 Digits</p> : <p></p>
                        } */}

                    </div>
                    <hr />
                    <Button color='primary' className='submit-btn'>Next</Button>

                </Form>

            </div>
        </StyleRoot>

    );
};


const style = {
    pad: {
        paddingLeft: 30
    },

    button: {
        color: '#ffffff',
        textAlign: 'center',
        border: 2,
        borderColor: '#ffffff',
        fontSize: 16,

        float: 'right',
        height: 40,
        width: 120,
        borderRadius: 5,
        position: 'absolute',
        right: 10,
        padding: 10,
        margin: 5

    },
    elementHolder: {
        display: 'flex',
    },
    inputTag: {
        width: 40,
        padding: 10,
        borderRadius: 5,
        margin: 3
    },
    selectTag: {
        width: 80,
        padding: 10,
        margin: 3,
        borderRadius: 5
    },
    topText: {
        color: '#000062'
    },
    generalText: {

        color: '#000062',
        textAlign: 'center',
        fontWeight: 500,
    }

}

export default FormCapture;