import React, { useState } from 'react';
import { Button, Container, Row, Modal, ModalFooter, ModalBody, ModalHeader } from 'reactstrap';

import '../scss/app.scss';
import Welcome from './components/Intro';
import Legal from './components/Legal';
import FormCapture from './components/FormCapture';
import ReviewInfo from './components/ReviewInfo';
import Verify from './components/Verify';
import Response from './components/Congradulatory';
import Authenticated from './components/Authenticated';
import BreadCrumbs from './components/Breadcrumbs';



//this below is our component that returns JSX to index.html 
function App() {

    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [pageCount, setpageCount] = useState(0);

    function increment() { setpageCount(pageCount + 1) };

    function correctPhone() { setpageCount(pageCount - 2) };


    let modalContent;
    let modalFooter;


    if (pageCount === 0) {
        modalContent = <Welcome />
        modalFooter = <ModalFooter> <Button color="primary" className="btn-md" onClick={increment}>Get Started</Button> </ModalFooter>

    } else if (pageCount === 1) {

        modalContent = <FormCapture currentPageCount={pageCount} onPageChange={increment} />
        modalFooter = ''


    } else if (pageCount === 2) {

        modalContent = <Legal currentPageCount={pageCount} onPageChange={increment} />

    } else if (pageCount === 3) {
        modalContent = <Verify currentPageCount={pageCount} onPageChange={increment} updatePhonenumber={correctPhone} />
        modalFooter = <ModalFooter> <Button color="primary" className="btn-md" onClick={increment}>Next</Button> </ModalFooter>

    } else if (pageCount === 4) {

        modalContent = <Authenticated currentPageCount={pageCount} onPageChange={increment} />
        modalFooter = <ModalFooter> <Button color="primary" className="btn-md" onClick={increment}>Next</Button> </ModalFooter>

    } else if (pageCount === 5) {

        modalContent = <ReviewInfo currentPageCount={pageCount} onPageChange={increment} />

    } else if (pageCount === 6) {
        modalContent = <Response />
    }

    return (
     
        <Container>
            <Row className="app">
                <Button outline color="primary" onClick={toggle}  >GET PREQUALIFIED</Button>
            </Row>
            <Modal isOpen={modal} toggle={toggle} size="lg">
                <ModalHeader toggle={toggle}> <BreadCrumbs currentPageCount={pageCount}/> </ModalHeader>
                <ModalBody>
                    {modalContent}
                </ModalBody>
                {modalFooter}
            </Modal>
        </Container>
      
    );

}


export default App;