import React from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

import { Button } from 'reactstrap';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}

const Response = () => {
    return (
        <StyleRoot>
            <div className="modalcontent" style={styles.slideInLeft} >
                <div className="getloan">
                    <h1 >CONGRATULATIONS!</h1>
                </div>
                <div className="prequal">
                    <p>You've been prequalified. Here are the details of your offer:</p>
                </div>
                <div className="approved">
                    <p><strong>You are approved for up to:</strong> </p>
                </div>
                <div className="range">
                    <h1> $35,000</h1>
                </div>
                <div className="approved">
                    <p><strong>Your APR range is:</strong> </p>
                </div>
                <div className="range">
                    <h3>1.5%-3.5%</h3>

                </div>

                <Button onClick={localStorage.clear()} className="ellipsis congrats-btn" color="primary" size="md" block >Shop for vehicles based on my Pre-qualification</Button>

            </div>
        </StyleRoot>

    );
}

export default Response;