import React from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}


const Welcome = () => {
    return (
        <StyleRoot>
            <div className="modalcontent" style={styles.slideInLeft} >
                <div className="getloan">
                    <h1 >GET PRE-QUALIFIED FOR A LOAN IN 30 Seconds</h1>
                    <h3>How it Works:</h3>
                    <ul>
                        <li>Well ask you for 2 pieces of information (no SSN needed)</li>
                        <li>Well Let you know what you have been qualified for</li>
                    </ul>
                    
                    <p className='ellipsis'>Pre-qualified shoppers see real terms and actual monthly payments for each vehicle</p>
                    </div>
                <div>
                    <div className="meter_image">
                    </div>
                    <h1 className="impact-txt">NO IMPACT TO YOUR CREDIT SCORE</h1>
                </div>
            </div>
        </StyleRoot>

    );
}

export default Welcome;