import React, { useState } from 'react';
import { Col, Row, Button, Form, FormGroup, Label, Input } from 'reactstrap';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}

const ReviewInfo = (props) => {

    const name = localStorage.getItem('first_name');
    const ml = localStorage.getItem('middle_name');
    const surname = localStorage.getItem('last_name');
    const zip = localStorage.getItem('zip');
    const dob = localStorage.getItem('birthday');
    const mystate = localStorage.getItem('state');
    const address = localStorage.getItem('address');
    const city = localStorage.getItem('city');
    const email = localStorage.getItem('email');
    const phonenumber = localStorage.getItem('phone_number');

    return (
        <StyleRoot>
            <div style={styles.slideInLeft} >


                <div className='getloan'>
                    <h1  >PLEASE REVIEW THE DETAILS BELOW</h1>
                </div>

                <div className='m-2 modalformcapture'>
                    <Form>
                        <Row form>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="Name">Name</Label>
                                    <Input type="text" name="name" id="name" defaultValue={name} />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="ML">ML</Label>
                                    <Input type="text" name="ml" id="ml" defaultValue={ml} />
                                </FormGroup>
                            </Col>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="Lastname">Lastname</Label>
                                    <Input type="text" name="lastname" id="lastname" defaultValue={surname} />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row form>

                            <Col md={5}>
                                <FormGroup>
                                    <Label for="street">Street Address</Label>
                                    <Input type="text" name="street" id="street" defaultValue={address} />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="Number">#</Label>
                                    <Input type="text" name="number" id="number" defaultValue={phonenumber} />
                                </FormGroup>
                            </Col>
                            <Col md={5}>
                                <FormGroup>
                                    <Label for="City">City</Label>
                                    <Input type="text" name="city" id="city" defaultValue={city} className='green' />
                                </FormGroup>
                            </Col>
                        </Row>

                        <Row form>
                            <Col md={6}>
                                <FormGroup>
                                    <Label for="Dob">Date of Birth</Label>
                                    <Input
                                        type="text"
                                        name="date"
                                        id="dateofbirth"
                                      
                                        defaultValue = {dob}
                                    />
                                </FormGroup>
                            </Col>
                            <Col md={4}>
                                <FormGroup>
                                    <Label for="State">State</Label>
                                    <Input type="text" name="state" id="State" defaultValue={mystate} />
                                </FormGroup>
                            </Col>
                            <Col md={2}>
                                <FormGroup>
                                    <Label for="Zip">Zip</Label>
                                    <Input type="text" name="zip" id="Zip" defaultValue={zip} />
                                </FormGroup>
                            </Col>
                        </Row>

                        <Button onClick={props.onPageChange} className='submit-btn' color='primary'>Submit</Button>
                    </Form>

                </div>
            </div>
        </StyleRoot>
    );
}

export default ReviewInfo;
