import React, { useState } from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

import { Col, Row, Button, Form, FormGroup, Input } from 'reactstrap';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')

    }
}

const LegalInfo = (props) => {

    const [checked, setChecked] = useState(false)
    const handleClick = () => setChecked(!checked)
    const [fetching, setFetching] = useState(false);

    const loader = <div class="spinner-border text-light" role="status"></div>

    const legalStuff = "It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like).It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like). It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like)."


    const submitInfo = async () => {
        const phonenumber = localStorage.getItem('myPhone');
        const id = localStorage.getItem('id');

        const userData = {
            phonenumber: phonenumber,
            ssn: id
        };

        var string = JSON.stringify(userData);
        console.log(string);

        await fetch('https://qubedlab-payphone.herokuapp.com/api/test/prefilldemo', {
            mode: 'cors',
            method: 'POST',
            body: string,
            headers: {
                'Accept': 'application/json',
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin': '*'
            }
        }).then(res => res.json()).then((data) => {
            console.log(data);

            localStorage.setItem('first_name', data.first_name);
            localStorage.setItem('middle_name', data.middle_name);
            localStorage.setItem('last_name', data.last_name);
            localStorage.setItem('zip', data.zip);
            localStorage.setItem('birthday', data.birthday);
            localStorage.setItem('state', data.state);
            localStorage.setItem('address', data.address);
            localStorage.setItem('city', data.city);
            localStorage.setItem('email', data.email);
            localStorage.setItem('phone_number', data.phone_number);

        }).catch(error => {
            console.error('There was an error!', error);
        });
        props.onPageChange();

    }


    const smsAuth = () => {
        const url = 'https://qubedlab-payphone.herokuapp.com/api/test/testwebauth';
        const phonenumber = localStorage.getItem('myPhone');
        const id = localStorage.getItem('id');

        setFetching(true);

        const userData = {
            phonenumber: phonenumber,
            ssn: id,
            tokenid: "T79932",
            domainurl: "www.mtho.com",
        };

        var string = JSON.stringify(userData);
        console.log(string);

        fetch(url, {
            mode: 'cors',
            method: 'POST',
            body: string,
            headers: {
                'Accept': 'application/json',
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin': '*'
            }
        }).then(res => {
            if (res.status === 200) {
                console.log("SUCCESSS");
                submitInfo();
                // return res.json();
            } else if (res.status === 408) {
                console.log("SOMETHING WENT WRONG")
            }

        }).catch(error => {
            console.error('There was an error!', error);
        });

    }



    return (
        <StyleRoot>
            <div style={styles.slideInLeft} >
                <div className="getloan">
                    <h1 >LASTLY THE LEGAL STUFF....</h1>
                    <Form className="modalcontent" >
                        <FormGroup row>

                            <Col md={12}>
                                <Input className="scrollable" rows={7} disabled type="textarea" name="text" value={legalStuff} />
                            </Col>
                        </FormGroup>
                        <Row>
                            <FormGroup check inline>

                                <div className='btn-pad'>

                                    <Input onClick={handleClick} defaultChecked={checked} type="checkbox" />I agree to the terms and conditions

                                </div>
                            </FormGroup>


                        </Row>

                        <div id='submit-btn'>
                            <Button onClick={smsAuth} disabled={!checked} className='submit-btn b-4' color={!checked ? 'secondary' : 'primary'} size="md">{fetching ? loader : 'Submit'}</Button>
                        </div>


                    </Form>

                </div>

            </div>
        </StyleRoot>

    );
};

export default LegalInfo;