import React from 'react';
import Radium, { StyleRoot } from 'radium';
// import '../../scss/app.scss';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';

import Approved from '../../img/approved.png';

const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}

const Authenticated = () => {
    return (
        <StyleRoot>
            <div className="modalcontent" style={styles.slideInLeft} >
                <div className="getloan">
                    <h1 >You've Been Authenticated</h1>
                </div>
                <div className="d-flex justify-content-center m-3" >
                    <img src={Approved} alt="approved icon" />
                </div> 
                <div className="prequal">
                    <p>Please click <b>Next</b> to continue...</p>
                </div>     
            </div>
        </StyleRoot >

    );
}

export default Authenticated;