import React from 'react';
import '../../scss/app.scss';



const BreadCrumbs = (props) => {

    const selectedPage = props.currentPageCount;

    if (selectedPage === 0) {
        var bread1Selected = true;
        
    } else if (selectedPage === 1) {
        var bread2Selected = true;

    } else if (selectedPage === 2) {
        var bread3Selected = true;

    } else if (selectedPage === 3) {
        var bread4Selected = true;

    } else if (selectedPage === 4) {
        var bread5Selected = true;

    }
    else if (selectedPage === 5) {
        var bread6Selected = true;

    }else if (selectedPage === 6) {
        var bread7Selected = true;

    }

    
    return (

        <div className='breadcrumbs'>
            <a className={!bread1Selected ? "btn-crumb" : "selected-crumb"}   > <p>1</p> </a>
            <div className='crumbline'></div>
            <a className={!bread2Selected ? "btn-crumb" : "selected-crumb"} > <p>2</p> </a>
            <div className='crumbline'></div>
            <a className={!bread3Selected ? "btn-crumb" : "selected-crumb"} > <p>3</p> </a>
            <div className='crumbline'></div>
            <a className={!bread4Selected ? "btn-crumb" : "selected-crumb"}> <p>4</p> </a>
            <div className='crumbline'></div>
            <a className={!bread5Selected ? "btn-crumb" : "selected-crumb"} > <p>5</p> </a>
            <div className='crumbline'></div>
            <a className={!bread6Selected ? "btn-crumb" : "selected-crumb"} > <p>6</p> </a>
            <div className='crumbline'></div>
            <a className={!bread7Selected ? "btn-crumb" : "selected-crumb"} > <p>7</p> </a>
        </div>

    );
}



export default BreadCrumbs;