import React, { useState } from 'react';
import Radium, { StyleRoot } from 'radium';
import '../../scss/app.scss';
import slideInLeft from 'react-animations/lib/slide-in-left';
import { Button, Alert } from 'reactstrap';

import VerifyImg from '../../img/verify.png';


const styles = {
    slideInLeft: {
        animation: 'x 0.7s',
        animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
    }
}

const Verify = (props) => {
    const [visible, setVisible] = useState(false);

    const onDismiss = () => setVisible(false);
    const phonenumber = localStorage.getItem('myPhone');
    const id = localStorage.getItem('id');
    const lastFourDigits = phonenumber.substring(8, 12);

    const submitInfo = async () => {

        const userData = {
            phonenumber: phonenumber,
            ssn: id
        };

        var string = JSON.stringify(userData);
        console.log(string);

        await fetch('https://qubedlab-payphone.herokuapp.com/api/test/prefilldemo', {
            mode: 'cors',
            method: 'POST',
            body: string,
            headers: {
                'Accept': 'application/json',
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin': '*'
            }
        }).then(res => res.json()).then((data) => {
            console.log(data);

            localStorage.removeItem('myPhone');
            localStorage.removeItem('id');

            localStorage.setItem('first_name', data.first_name);
            localStorage.setItem('middle_name', data.middle_name);
            localStorage.setItem('last_name', data.last_name);
            localStorage.setItem('zip', data.zip);
            localStorage.setItem('birthday', data.birthday);
            localStorage.setItem('state', data.state);
            localStorage.setItem('address', data.address);
            localStorage.setItem('city', data.city);
            localStorage.setItem('email', data.email);
            localStorage.setItem('phone_number', data.phone_number);

        }).catch(error => {
            console.error('There was an error!', error);
        });
        props.onPageChange();

    }


    const smsAuth = () => {
        const url = 'https://qubedlab-payphone.herokuapp.com/api/test/testwebauth';

        const userData = {
            phonenumber: phonenumber,
            ssn: id,
            tokenid: "T79932",
            domainurl: "www.mtho.com",
        };

        var string = JSON.stringify(userData);
        console.log(string);

        fetch(url, {
            mode: 'cors',
            method: 'POST',
            body: string,
            headers: {
                'Accept': 'application/json',
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin': '*'
            }
        }).then(res => {
            if (res.status === 200) {
                console.log("SUCCESSS");
                submitInfo();
                // return res.json();
            } else if (res.status === 408) {
                console.log("SOMETHING WENT WRONG")
            }

        }).catch(error => {
            console.error('There was an error!', error);
        });

    }

    const pencilIcon = <svg class="bi bi-pencil" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path fillRule="evenodd" d="M11.293 1.293a1 1 0 0 1 1.414 0l2 2a1 1 0 0 1 0 1.414l-9 9a1 1 0 0 1-.39.242l-3 1a1 1 0 0 1-1.266-1.265l1-3a1 1 0 0 1 .242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z" />
        <path fillRule="evenodd" d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 0 0 .5.5H4v.5a.5.5 0 0 0 .5.5H5v.5a.5.5 0 0 0 .5.5H6v-1.5a.5.5 0 0 0-.5-.5H5v-.5a.5.5 0 0 0-.5-.5H3z" />
    </svg>

    return (
        <StyleRoot>
            <div className="modalcontent " style={styles.slideInLeft} >
                {
                    <Alert color="info" isOpen={visible} toggle={onDismiss}>
                        We have resent SMS to your mobile succesfully!
                   </Alert>
                }
                <div className="getloan">
                    <h1 >Please Check Your Phone</h1>
                </div>
                <div className="prequal ">
                    <p>We need to verify your identity</p>
                </div>
                <div className="d-flex justify-content-center p-2" >
                    <img src={VerifyImg} alt="approved icon" />
                </div>
                <div className="prequal p-2">
                    <p>A text message with a link was just sent to the phone ending in</p>
                </div>
                <div className="approved ">
                    <Button className='center' color="link" onClick={props.updatePhonenumber}> <div className='d-flex justify-content-center' >{lastFourDigits} <div className='pencil'>{pencilIcon} </div></div> </Button>
                </div>
                <div className="d-flex justify-content-center">
                    <Button onClick={() => { smsAuth(); setVisible(true) }} className="btn-xs disabledOpacity" > I did not receive that text</Button>
                </div>
            </div>


        </StyleRoot>

    );
}

export default Verify;